import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv5Module } from '../../Models/cv5/cv5.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv5Service {
  private data_cv5 = new BehaviorSubject<any>( new Cv5Module() );
  load_data_cv5 = this.data_cv5.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv5( json_data_cv5, url){
    json_data_cv5['url']= url;
    this.data_cv5.next(json_data_cv5);
  }

  saveJsondata(url:string,json_data_cv5){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv5);
  }
}
