import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv14Module } from '../../Models/cv14/cv14.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv14Service {
  private data_cv14 = new BehaviorSubject<any>( new Cv14Module() );
  load_data_cv14 = this.data_cv14.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv14( json_data_cv14, url){
    json_data_cv14['url']= url;
    this.data_cv14.next(json_data_cv14);
  }

  saveJsondata(url:string,json_data_cv14){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv14);
  }
}
