import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv13Service } from '../../../services/cv13/info-cv13.service';
import { Cv13Module,strategie } from '../../../Models/cv13/cv13.module'

@Component({
    selector: '.app-blog-componentv13',
    templateUrl: './blog_component_v13.component.html',
    styleUrls: ['./blog_component_v13.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV13 implements OnInit  {
    public BLOCK = new strategie();
    public DATACV13= new Cv13Module();
    public url:any;

    constructor( private infocv13service: InfoCv13Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV13
        this.infocv13service.load_data_cv13.subscribe( 
            data => {
                this.DATACV13.title = data.title;
                this.DATACV13.description = data.description;
                this.DATACV13.strategies = data.strategies;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV13)
    }

    // function will send data to info cv13 service
    getDataCv13(data,url){
        this.infocv13service.loadJsondata_cv13(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv13.json';
        console.log(link);
        this.infocv13service.saveJsondata(link,this.DATACV13)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addBlock(){
        this.DATACV13.strategies.push(this.BLOCK);
        this.saveData();
    }
    deleteBlock(index){
        this.DATACV13.strategies.splice(index,1);
    }
    openModal(){
        document.getElementById("componentV13").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV13").style.display = "none"; 
    }
 }