import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv8Module } from '../../Models/cv8/cv8.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv8Service {
  private data_cv8 = new BehaviorSubject<any>( new Cv8Module() );
  load_data_cv8 = this.data_cv8.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv8( json_data_cv8, url){
    json_data_cv8['url']= url;
    this.data_cv8.next(json_data_cv8);
  }

  saveJsondata(url:string,json_data_cv8){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv8);
  }
}
