import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv10Module } from '../../Models/cv10/cv10.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv10Service {
  private data_cv10 = new BehaviorSubject<any>( new Cv10Module() );
  load_data_cv10 = this.data_cv10.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv10( json_data_cv10, url){
    json_data_cv10['url']= url;
    this.data_cv10.next(json_data_cv10);
  }

  saveJsondata(url:string,json_data_cv10){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv10);
  }
}
