import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv13Module } from '../../Models/cv13/cv13.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv13Service {
  private data_cv13 = new BehaviorSubject<any>( new Cv13Module() );
  load_data_cv13 = this.data_cv13.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv13( json_data_cv13, url){
    json_data_cv13['url']= url;
    this.data_cv13.next(json_data_cv13);
  }

  saveJsondata(url:string,json_data_cv13){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv13);
  }
}
