import { Component, OnInit } from '@angular/core';
import { ServFooterService } from '../../../services/footer/serv-footer.service';
import { ModelFooterModule } from '../../../Models/footer/model-footer/model-footer.module';


@Component({
    selector: '.app-footerv1',
    templateUrl: './footer_v1.component.html',
    styleUrls: ['./footer_v1.component.css']
})

export class FooterV1 implements OnInit {
    public dataFooter= new ModelFooterModule();
    public url:any;
    public links: any;

    constructor( private footservice: ServFooterService ){ }
    ngOnInit() {
        this.footservice.load_data_cv7.subscribe( 
            data => {
                this.dataFooter.desc = data.desc;
                this.dataFooter.title = data.title;
                this.dataFooter.name = data.name;
                this.dataFooter.socialLinks = data.socialLinks;
                this.dataFooter.navbars = data.navbars;
                this.links = data.socialLinks;
            });
    }


    getData(data){
        this.footservice.loadJsondata_cv7(data);
    }

    saveData(){
        this.deleteNullSocial();
    
        this.dataFooter.socialLinks = this.links;
        
        let link = '/footer.json';
        this.footservice.saveJsondata(link,this.dataFooter)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }

    closeModal(){
        document.getElementById("footerV1").style.display = "none"; 
    }

    selectSocial(social:string){
        document.getElementById("defaultInput").classList.add("d-none");
        if(social=="Facebook" ){
            document.getElementById("socialNav").innerHTML='<i class="fab fa-facebook-square">';
            document.getElementById("twtInput").classList.add("d-none");
            document.getElementById("insInput").classList.add("d-none");
            document.getElementById("ytbInput").classList.add("d-none");
            document.getElementById("fbInput").classList.remove("d-none");
        }else if(social=="Instagram"){
            document.getElementById("socialNav").innerHTML='<i class="fab fa-instagram-square">';
            document.getElementById("twtInput").classList.add("d-none");
            document.getElementById("fbInput").classList.add("d-none");
            document.getElementById("ytbInput").classList.add("d-none");
            document.getElementById("insInput").classList.remove("d-none");
        }else if(social=="Twitter"){
            document.getElementById("socialNav").innerHTML='<i class="fab fa-twitter-square">';
            document.getElementById("fbInput").classList.add("d-none");
            document.getElementById("insInput").classList.add("d-none");
            document.getElementById("ytbInput").classList.add("d-none");
            document.getElementById("twtInput").classList.remove("d-none");
        }else{
            document.getElementById("socialNav").innerHTML='<i class="fab fa-youtube-square">';
            document.getElementById("twtInput").classList.add("d-none");
            document.getElementById("insInput").classList.add("d-none");
            document.getElementById("fbInput").classList.add("d-none");
            document.getElementById("ytbInput").classList.remove("d-none");
        }
       
    }

    findSocial(social){
        
        
        for(var i = 0; i < this.links.length; i++)
        {
            if(this.links[i].socialName == social)
            {
                return this.links[i];
            }
        }


    }

    deleteNullSocial(){
        var jsonAlternative = [] ; 
        var facebook, twitter,instagram,youtube;
        facebook = (<HTMLInputElement>document.getElementById("fbInput")).value;
        twitter = (<HTMLInputElement>document.getElementById("twtInput")).value;
        instagram = (<HTMLInputElement>document.getElementById("insInput")).value;
        youtube = (<HTMLInputElement>document.getElementById("ytbInput")).value;
        
        this.checkSocialExists('Facebook',facebook);
        this.checkSocialExists('Twitter',twitter);
        this.checkSocialExists('Instagram',instagram);
        this.checkSocialExists('Youtube',youtube);

        for(var i = 0; i < this.links.length; i++)
        {
            
            if(this.links[i].socialLink)
            {
                jsonAlternative.push(this.links[i]);
            }
        }
        this.links = jsonAlternative;

    }

    checkSocialExists(social, link){
        
        for(var i = 0; i < this.links.length; i++)
        {
            if(this.links[i].socialName == social && this.links[i].socialLink == link)
            {
                return;
            }
        }

        var newLink = {
            socialName: social,
            socialLink : link,
            socialIcon : social+'.png'
        }
        
        this.links.push(newLink);
        
    }
}