import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv2Module } from '../../Models/cv2/cv2.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv2Service {
  private data_cv2 = new BehaviorSubject<any>( new Cv2Module() );
  load_data_cv2 = this.data_cv2.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv2( json_data_cv2, url){
    json_data_cv2['url']= url;
    this.data_cv2.next(json_data_cv2);
  }

  saveJsondata(url:string,json_data_cv2){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv2);
  }
}
