import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';



@NgModule({
  declarations: [],
  imports: [
    CommonModule
  ]
})
export class ModelFooterModule {
  name : string;
  title: string;
  desc: string;
  background: string;
  socialLinks:[
    {
      socialName: string;
      socialIcon: string;
      socialLink:  string;
    }
  ];
  navbars:[
    {
      value: string;
      display: string;
      children:[
        {
          title: string;
          link: string;
        }
      ]
    }
  ]
}
