import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Servicev1Module } from '../../Models/services/servicev1/servicev1.module';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoSv1Service {
  public host:string = "http://localhost:3000";
  private data_sv1 = new BehaviorSubject<any>( new Servicev1Module() );
  load_data_sv1 = this.data_sv1.asObservable();
  constructor(private httpClient:HttpClient) { }

    // will update vriable value by data recieved
    loadJsondata_sv1( json_data_sv1, url){
      json_data_sv1['url']= url;
      this.data_sv1.next(json_data_sv1);
    }

    saveJsondata(url:string,json_data_sv1){
      let link = this.host+"/api/update?url="+url;
      return this.httpClient.post(link, json_data_sv1);
    }
}
