import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv15Module } from '../../Models/cv15/cv15.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv15Service {
  private data_cv15 = new BehaviorSubject<any>( new Cv15Module() );
  load_data_cv15 = this.data_cv15.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv15( json_data_cv15, url){
    json_data_cv15['url']= url;
    this.data_cv15.next(json_data_cv15);
  }

  saveJsondata(url:string,json_data_cv15){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv15);
  }
}
