import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv5Service } from '../../../services/cv5/info-cv5.service';
import { Cv5Module } from '../../../Models/cv5/cv5.module'

@Component({
    selector: '.app-blog-componentv5',
    templateUrl: './blog_component_v5.component.html',
    styleUrls: ['./blog_component_v5.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV5 implements OnInit  {

    public DATACV5= new Cv5Module();
    public url:any;

    constructor( private infocv5service: InfoCv5Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV5
        this.infocv5service.load_data_cv5.subscribe( 
            data => {
                this.DATACV5.description = data.description;
                this.DATACV5.title_button = data.title_button;
                this.DATACV5.link_button = data.link_button;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV5)
    }

    // function will send data to info cv5 service
    getDataCv5(data,url){
        this.infocv5service.loadJsondata_cv5(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv5.json';
        console.log(link);
        this.infocv5service.saveJsondata(link,this.DATACV5)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV5").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV5").style.display = "none"; 
    }
 }