import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Servicev2Module } from '../../Models/services/servicev2/servicev2.module';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoSv2Service {
  public host:string = "http://localhost:3000";
  private data_sv2 = new BehaviorSubject<any>( new Servicev2Module() );
  load_data_sv2 = this.data_sv2.asObservable();
  constructor(private httpClient:HttpClient) { }

    // will update vriable value by data recieved
    loadJsondata_sv2( json_data_sv2, url){
      json_data_sv2['url']= url;
      this.data_sv2.next(json_data_sv2);
    }

    saveJsondata(url:string,json_data_sv2){
      let link = this.host+"/api/update?url="+url;
      return this.httpClient.post(link, json_data_sv2);
    }
}
