import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv16Service } from '../../../services/cv16/info-cv16.service';
import { Cv16Module } from '../../../Models/cv16/cv16.module'

@Component({
    selector: '.app-blog-componentv16',
    templateUrl: './blog_component_v16.component.html',
    styleUrls: ['./blog_component_v16.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV16 implements OnInit  {

    public DATACV16= new Cv16Module();
    public url:any;

    constructor( private infocv16service: InfoCv16Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV16
        this.infocv16service.load_data_cv16.subscribe( 
            data => {
                this.DATACV16.title = data.title;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV16)
    }

    // function will send data to info cv16 service
    getDataCv16(data,url){
        this.infocv16service.loadJsondata_cv16(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv16.json';
        console.log(link);
        this.infocv16service.saveJsondata(link,this.DATACV16)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV16").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV16").style.display = "none"; 
    }
 }