import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv9Module } from '../../Models/cv9/cv9.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv9Service {
  private data_cv9 = new BehaviorSubject<any>( new Cv9Module() );
  load_data_cv9 = this.data_cv9.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv9( json_data_cv9, url){
    json_data_cv9['url']= url;
    this.data_cv9.next(json_data_cv9);
  }

  saveJsondata(url:string,json_data_cv9){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv9);
  }
}
