import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv10Service } from '../../../services/cv10/info-cv10.service';
import { Cv10Module, statistic } from '../../../Models/cv10/cv10.module'

@Component({
    selector: '.app-blog-componentv10',
    templateUrl: './blog_component_v10.component.html',
    styleUrls: ['./blog_component_v10.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV10 implements OnInit  {
    public STATISTIC = new statistic();
    public DATACV10= new Cv10Module();
    public url:any;

    constructor( private infocv10service: InfoCv10Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV10
        this.infocv10service.load_data_cv10.subscribe( 
            data => {
                this.DATACV10.title = data.title;
                this.DATACV10.statistics = data.statistics;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV10)
    }

    // function will send data to info cv10 service
    getDataCv10(data,url){
        this.infocv10service.loadJsondata_cv10(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv10.json';
        console.log(link);
        this.infocv10service.saveJsondata(link,this.DATACV10)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addBlock(){
        this.DATACV10.statistics.push(this.STATISTIC);
        this.saveData();
    }
    deleteBlock(index){
        this.DATACV10.statistics.splice(index,1);
    }
    openModal(){
        document.getElementById("componentV10").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV10").style.display = "none"; 
    }
 }