import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv4Service } from '../../../services/cv4/info-cv4.service';
import { Cv4Module, image } from '../../../Models/cv4/cv4.module'

@Component({
    selector: '.app-blog-componentv4',
    templateUrl: './blog_component_v4.component.html',
    styleUrls: ['./blog_component_v4.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV4 implements OnInit  {
    public IMAGE = new image();
    public DATACV4= new Cv4Module();
    public url:any;

    constructor( private infocv4service: InfoCv4Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV4
        this.infocv4service.load_data_cv4.subscribe( 
            data => {
                this.DATACV4.title = data.title;
                this.DATACV4.description = data.description;
                this.DATACV4.images = data.images;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV4)
    }

    // function will send data to info cv4 service
    getDataCv4(data,url){
        this.infocv4service.loadJsondata_cv4(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv4.json';
        console.log(link);
        this.infocv4service.saveJsondata(link,this.DATACV4)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addBlock(){
        this.DATACV4.images.push(this.IMAGE);
        this.saveData();
    }
    deleteBlock(index){
        this.DATACV4.images.splice(index,1);
    }
    openModal(){
        document.getElementById("componentV4").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV4").style.display = "none"; 
    }
 }