import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { ModelFooterModule } from '../../Models/footer/model-footer/model-footer.module';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ServFooterService {
  private data_cv7 = new BehaviorSubject<any>( new ModelFooterModule() );
  load_data_cv7 = this.data_cv7.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }

  loadJsondata_cv7( json_data_cv7 ){
    this.data_cv7.next(json_data_cv7);
  }

  saveJsondata(url:string,json_data_cv7){
    let link = this.host+"/api/update?global=footer&url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv7);
  }
}
