import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv7Module } from '../../Models/cv7/cv7.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv7Service {
  private data_cv7 = new BehaviorSubject<any>( new Cv7Module() );
  load_data_cv7 = this.data_cv7.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv7( json_data_cv7, url){
    json_data_cv7['url']= url;
    this.data_cv7.next(json_data_cv7);
  }

  saveJsondata(url:string,json_data_cv7){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv7);
  }
}
