import { Component, HostListener , OnInit } from '@angular/core';
import * as dataMenu from '../../jsonFiles/menu.json';
import { MenuIteam } from 'src/app/Models/menu/menu.module';


@Component({
    selector: '.app-headerv2',
    templateUrl: './header_v2.component.html',
    styleUrls: ['./header_v2.component.css']
})

export class HeaderV2 implements OnInit {
    ngOnInit() {
    // will read all menuIteam from menu.json
    dataMenu.menuIteam.forEach(element => {
        this.menuIteams.push(element);
      });
    } 
  public menuIteams: MenuIteam[] = [];

    checkScroll = false;
    

    // check once if I scroll down or up -> changing color of header 
    @HostListener('window:scroll', ['$event']) onScrollEvent($event) {
        if(window.scrollY >= 10 && !this.checkScroll) {
            document.getElementById('nw-header2').classList.remove('black-op');
            document.getElementById('nw-header2').classList.add('dync-op');
            this.checkScroll = true;
        }else if(window.scrollY < 10 && this.checkScroll) {
            document.getElementById('nw-header2').classList.remove('dync-op');
            document.getElementById('nw-header2').classList.add('black-op');
            this.checkScroll = false;
        }
    }
    // END : check once if I scroll down or up -> changing color of header
 
}