import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv6Module } from '../../Models/cv6/cv6.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv6Service {
  private data_cv6 = new BehaviorSubject<any>( new Cv6Module() );
  load_data_cv6 = this.data_cv6.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv6( json_data_cv6, url){
    json_data_cv6['url']= url;
    this.data_cv6.next(json_data_cv6);
  }

  saveJsondata(url:string,json_data_cv6){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv6);
  }
}
