import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv1Module } from '../../Models/cv1/cv1.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv1Service {
  private data_cv1 = new BehaviorSubject<any>( new Cv1Module() );
  load_data_cv1 = this.data_cv1.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv1( json_data_cv1, url){
    json_data_cv1['url']= url;
    this.data_cv1.next(json_data_cv1);
  }

  saveJsondata(url:string,json_data_cv1){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv1);
  }
}
