import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv7Service } from '../../../services/cv7/info-cv7.service';
import { Cv7Module } from '../../../Models/cv7/cv7.module'

@Component({
    selector: '.app-blog-componentv7',
    templateUrl: './blog_component_v7.component.html',
    styleUrls: ['./blog_component_v7.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV7 implements OnInit  {

    public DATACV7= new Cv7Module();
    public url:any;

    constructor( private infocv7service: InfoCv7Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV7
        this.infocv7service.load_data_cv7.subscribe( 
            data => {
                this.DATACV7.background_image = data.background_image;
                this.DATACV7.title = data.title;
                this.DATACV7.sub_title = data.sub_title;
                this.DATACV7.desc = data.desc;
                this.DATACV7.text_button = data.text_button;
                this.DATACV7.url_button = data.url_button;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV7)
    }

    // function will send data to info cv7 service
    getDataCv7(data,url){
        this.infocv7service.loadJsondata_cv7(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv7.json';
        console.log(link);
        this.infocv7service.saveJsondata(link,this.DATACV7)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV7").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV7").style.display = "none"; 
    }
 }