import { Component, HostListener } from '@angular/core';



@Component({
    selector: '.app-headerv1',
    templateUrl: './header_v1.component.html',
    styleUrls: ['./header_v1.component.css']
})

export class HeaderV1 { 

    checkScroll = false;
    

    
    // check once if I scroll down or up -> changing color of header 
    @HostListener('window:scroll', ['$event']) onScrollEvent($event) {
        if(window.scrollY >= 10 && !this.checkScroll) {
            document.getElementById('nw-header').classList.remove('white-op');
            document.getElementById('nw-header').classList.add('primary-op');
            this.checkScroll = true;
        }else if(window.scrollY < 10 && this.checkScroll) {
            document.getElementById('nw-header').classList.remove('primary-op');
            document.getElementById('nw-header').classList.add('white-op');
            this.checkScroll = false;
        }
    }
    // END : check once if I scroll down or up -> changing height and color of header
 
}