import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv15Service } from '../../../services/cv15/info-cv15.service';
import { Cv15Module } from '../../../Models/cv15/cv15.module'

@Component({
    selector: '.app-blog-componentv15',
    templateUrl: './blog_component_v15.component.html',
    styleUrls: ['./blog_component_v15.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV15 implements OnInit  {

    public DATACV15= new Cv15Module();
    public url:any;

    constructor( private infocv15service: InfoCv15Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV15
        this.infocv15service.load_data_cv15.subscribe( 
            data => {
                this.DATACV15.title = data.title;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV15)
    }

    // function will send data to info cv15 service
    getDataCv15(data,url){
        this.infocv15service.loadJsondata_cv15(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv15.json';
        console.log(link);
        this.infocv15service.saveJsondata(link,this.DATACV15)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV15").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV15").style.display = "none"; 
    }
 }