import { Component, OnInit, Injectable } from '@angular/core';
import { InfoSv1Service } from '../../../services/servicev1/info-sv1.service';
import { Servicev1Module, service } from '../../../Models/services/servicev1/servicev1.module';

@Component({
    selector: '.app-servicev1',
    templateUrl: './service_v1.component.html',
    styleUrls: ['./service_v1.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class ServiceV1 implements OnInit {
    public SERVICE =new service();
    public DATASV1= new Servicev1Module();
    public DATASV1_COPY= new Servicev1Module();
    public url:any;

    constructor(private infoSv1Service: InfoSv1Service){ }
    ngOnInit(){
        this.infoSv1Service.load_data_sv1.subscribe(
            data =>{
                this.DATASV1.title = data.title;
                this.DATASV1.description = data.description;
                this.DATASV1.services = data.services;
                //url
                this.url = data.url;
            }
        );
    }
    getDataSv1(data,url){
        this.infoSv1Service.loadJsondata_sv1(data,url);
    }
    saveData(){
        let link = '/'+this.url+'/app-servicev1.json';
        this.infoSv1Service.saveJsondata(link,this.DATASV1)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addService(){
        this.DATASV1.services.push(this.SERVICE);
        this.saveData();
    }
    deleteService(index){
        //console.log(this.DATASV1_COPY);
        this.DATASV1.services.splice(index,1);
    }
    openModal(){
        document.getElementById("serviceV1").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("serviceV1").style.display = "none"; 
    }
    
 }