import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv3Service } from '../../../services/cv3/info-cv3.service';
import { Cv3Module, block } from '../../../Models/cv3/cv3.module'

@Component({
    selector: '.app-blog-componentv3',
    templateUrl: './blog_component_v3.component.html',
    styleUrls: ['./blog_component_v3.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV3 implements OnInit  {
    public BLOCK = new block();
    public DATACV3= new Cv3Module();
    public url:any;

    constructor( private infocv3service: InfoCv3Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV3
        this.infocv3service.load_data_cv3.subscribe( 
            data => {
                this.DATACV3.title = data.title;
                this.DATACV3.description = data.description;
                this.DATACV3.blocks = data.blocks;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV3)
    }

    // function will send data to info cv3 service
    getDataCv3(data,url){
        this.infocv3service.loadJsondata_cv3(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv3.json';
        console.log(link);
        this.infocv3service.saveJsondata(link,this.DATACV3)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addBlock(){
        this.DATACV3.blocks.push(this.BLOCK);
        this.saveData();
    }
    deleteBlock(index){
        this.DATACV3.blocks.splice(index,1);
    }
    openModal(){
        document.getElementById("componentV3").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV3").style.display = "none"; 
    }
 }