import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv12Module } from '../../Models/cv12/cv12.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv12Service {
  private data_cv12 = new BehaviorSubject<any>( new Cv12Module() );
  load_data_cv12 = this.data_cv12.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv12( json_data_cv12, url){
    json_data_cv12['url']= url;
    this.data_cv12.next(json_data_cv12);
  }

  saveJsondata(url:string,json_data_cv12){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv12);
  }
}
