import { Component, OnInit } from '@angular/core';
import * as dataCv11 from './jsonFiles/app-blog-componentv11.json';
import { BlogComponentV11 } from '../.././components/blog_component/blog_component_v11/blog_component_v11.component';
import * as dataCv15 from './jsonFiles/app-blog-componentv15.json';
import { BlogComponentV15 } from '../.././components/blog_component/blog_component_v15/blog_component_v15.component';
import * as DATASV1 from './jsonFiles/app-servicev1.json';
import { ServiceV1 } from '../.././components/service/service_v1/service_v1.component';
import * as DATASV2 from './jsonFiles/app-servicev2.json';
import { ServiceV2 } from '../.././components/service/service_v2/service_v2.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-service',
  templateUrl: './service.component.html',
  styleUrls: ['./service.component.css']
})
export class ServiceComponent implements OnInit {

  constructor(public blogcomponentv11: BlogComponentV11,
              public blogcomponentv15: BlogComponentV15,
              public serviceV1: ServiceV1,
              public serviceV2: ServiceV2,
              
              private route: ActivatedRoute,) { }

  dataComponenetV11: any = (dataCv11 as any).default;
  dataComponenetV15: any = (dataCv15 as any).default;
  dataServiceV1: any = (DATASV1 as any).default;
  dataServiceV2: any = (DATASV2 as any).default;

  ngOnInit(): void {
    var text= this.route.routeConfig.component.name; // component Name 
    var folderJson = text.split('Com')[0].toLowerCase()+"/jsonFiles"; // convert component Name to component Folder + "/jsonFiles"
    //console.log(folderJson);
    this.blogcomponentv11.getDataCv11(this.dataComponenetV11,folderJson);
    this.blogcomponentv15.getDataCv15(this.dataComponenetV15,folderJson);
    this.serviceV1.getDataSv1(this.dataServiceV1,folderJson);
    this.serviceV2.getDataSv2(this.dataServiceV2,folderJson);
  }

}
