import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv1Service } from '../../../services/cv1/info-cv1.service';
import { Cv1Module, image } from '../../../Models/cv1/cv1.module'

@Component({
    selector: '.app-blog-componentv1',
    templateUrl: './blog_component_v1.component.html',
    styleUrls: ['./blog_component_v1.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV1 implements OnInit  {
    public IMAGE = new image();
    public DATACV1= new Cv1Module();
    public url:any;

    constructor( private infocv1service: InfoCv1Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV1
        this.infocv1service.load_data_cv1.subscribe( 
            data => {
                this.DATACV1.title = data.title;
                this.DATACV1.description = data.description;
                this.DATACV1.images = data.images;
                this.url = data.url;
            });
        // END
        ////console.log(this.DATACV1)
    }

    // function will send data to info cv1 service
    getDataCv1(data,url){
        this.infocv1service.loadJsondata_cv1(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv1.json';
        console.log(link);
        this.infocv1service.saveJsondata(link,this.DATACV1)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addBlock(){
        this.DATACV1.images.push(this.IMAGE);
        this.saveData();
    }
    deleteBlock(index){
        this.DATACV1.images.splice(index,1);
    }
    openModal(){
        document.getElementById("componentV1").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV1").style.display = "none"; 
    }
 }