import { Component, OnInit, Injectable } from '@angular/core';
import { InfoSv2Service } from '../../../services/servicev2/info-sv2.service';
import { Servicev2Module, service } from '../../../Models/services/servicev2/servicev2.module';

@Component({
    selector: '.app-servicev2',
    templateUrl: './service_v2.component.html',
    styleUrls: ['./service_v2.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class ServiceV2 implements OnInit {
    public SERVICE =new service();
    public DATASV2= new Servicev2Module();
    public DATASV2_COPY= new Servicev2Module();
    public url:any;

    constructor(private infoSv2Service: InfoSv2Service){ }
    ngOnInit(){
        this.infoSv2Service.load_data_sv2.subscribe(
            data =>{
                this.DATASV2.title = data.title;
                this.DATASV2.sub_title = data.sub_title;
                this.DATASV2.description = data.description;
                this.DATASV2.services = data.services;
                //url
                this.url = data.url;
            }
        );
    }
    getDataSv2(data,url){
        this.infoSv2Service.loadJsondata_sv2(data,url);
    }
    saveData(){
        let link = '/'+this.url+'/app-servicev2.json';
        this.infoSv2Service.saveJsondata(link,this.DATASV2)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addService(){
        this.DATASV2.services.push(this.SERVICE);
        this.saveData();
    }
    deleteService(index){
        //console.log(this.DATASV2_COPY);
        this.DATASV2.services.splice(index,1);
    }
    openModal(){
        document.getElementById("serviceV2").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("serviceV2").style.display = "none"; 
    }
    
 }