import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv2Service } from '../../../services/cv2/info-cv2.service';
import { Cv2Module, block } from '../../../Models/cv2/cv2.module'

@Component({
    selector: '.app-blog-componentv2',
    templateUrl: './blog_component_v2.component.html',
    styleUrls: ['./blog_component_v2.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV2 implements OnInit  {
    public BLOCK = new block();
    public DATACV2= new Cv2Module();
    public url:any;

    constructor( private infocv2service: InfoCv2Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV2
        this.infocv2service.load_data_cv2.subscribe( 
            data => {
                this.DATACV2.title = data.title;
                this.DATACV2.description = data.description;
                this.DATACV2.blocks = data.blocks;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV2)
    }

    // function will send data to info cv2 service
    getDataCv2(data,url){
        this.infocv2service.loadJsondata_cv2(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv2.json';
        console.log(link);
        this.infocv2service.saveJsondata(link,this.DATACV2)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addBlock(){
        this.DATACV2.blocks.push(this.BLOCK);
        this.saveData();
    }
    deleteBlock(index){
        this.DATACV2.blocks.splice(index,1);
    }
    openModal(){
        document.getElementById("componentV2").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV2").style.display = "none"; 
    }
 }