import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv16Module } from '../../Models/cv16/cv16.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv16Service {
  private data_cv16 = new BehaviorSubject<any>( new Cv16Module() );
  load_data_cv16 = this.data_cv16.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv16( json_data_cv16, url){
    json_data_cv16['url']= url;
    this.data_cv16.next(json_data_cv16);
  }

  saveJsondata(url:string,json_data_cv16){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv16);
  }
}
