import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv14Service } from '../../../services/cv14/info-cv14.service';
import { Cv14Module } from '../../../Models/cv14/cv14.module'

@Component({
    selector: '.app-blog-componentv14',
    templateUrl: './blog_component_v14.component.html',
    styleUrls: ['./blog_component_v14.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV14 implements OnInit  {

    public DATACV14= new Cv14Module();
    public url:any;

    constructor( private infocv14service: InfoCv14Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV14
        this.infocv14service.load_data_cv14.subscribe( 
            data => {
                this.DATACV14.title = data.title;
                this.DATACV14.description = data.description;
                this.DATACV14.location = data.location;
                this.DATACV14.phoneNumber1 = data.phoneNumber1;
                this.DATACV14.phoneNumber2 = data.phoneNumber2;
                this.DATACV14.email = data.email;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV14)
    }

    // function will send data to info cv14 service
    getDataCv14(data,url){
        this.infocv14service.loadJsondata_cv14(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv14.json';
        console.log(link);
        this.infocv14service.saveJsondata(link,this.DATACV14)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV14").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV14").style.display = "none"; 
    }
 }