import { Component, OnInit } from '@angular/core';
import * as dataCv7 from './jsonFiles/app-blog-componentv7.json';
import * as DATASV2 from './jsonFiles/app-servicev2.json';
import { BlogComponentV7 } from '../.././components/blog_component/blog_component_v7/blog_component_v7.component';
import { ServiceV2 } from '../.././components/service/service_v2/service_v2.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-ici',
  templateUrl: './ici.component.html',
  styleUrls: ['./ici.component.css']
})
export class IciComponent implements OnInit {

  constructor( public blogcomponentv7: BlogComponentV7, private route: ActivatedRoute,public servicev2: ServiceV2) { }

  dataComponenetV7: any = (dataCv7 as any).default; // get data from json file app-blog-componentv7.json
  dataServiceV2: any =(DATASV2 as any).default; // get data from json file app-servicev2.json
  
  ngOnInit(){
    
    var text= this.route.routeConfig.component.name; // component Name 
    var folderJson = text.split('Com')[0].toLowerCase()+"/jsonFiles"; // convert component Name to component Folder + "/jsonFiles"
    console.log(folderJson)
    this.blogcomponentv7.getDataCv7(this.dataComponenetV7,folderJson);
    this.servicev2.getDataSv2(this.dataServiceV2,folderJson);
  }

}