import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv12Service } from '../../../services/cv12/info-cv12.service';
import { Cv12Module } from '../../../Models/cv12/cv12.module'

@Component({
    selector: '.app-blog-componentv12',
    templateUrl: './blog_component_v12.component.html',
    styleUrls: ['./blog_component_v12.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV12 implements OnInit  {

    public DATACV12= new Cv12Module();
    public url:any;

    constructor( private infocv12service: InfoCv12Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV12
        this.infocv12service.load_data_cv12.subscribe( 
            data => {
                this.DATACV12.title = data.title;
                this.DATACV12.sub_title = data.sub_title;
                this.DATACV12.description1 = data.description1;
                this.DATACV12.description2 = data.description2;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV12)
    }

    // function will send data to info cv12 service
    getDataCv12(data,url){
        this.infocv12service.loadJsondata_cv12(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv12.json';
        console.log(link);
        this.infocv12service.saveJsondata(link,this.DATACV12)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV12").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV12").style.display = "none"; 
    }
 }