import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv11Service } from '../../../services/cv11/info-cv11.service';
import { Cv11Module } from '../../../Models/cv11/cv11.module'

@Component({
    selector: '.app-blog-componentv11',
    templateUrl: './blog_component_v11.component.html',
    styleUrls: ['./blog_component_v11.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV11 implements OnInit  {

    public DATACV11= new Cv11Module();
    public url:any;

    constructor( private infocv11service: InfoCv11Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV11
        this.infocv11service.load_data_cv11.subscribe( 
            data => {
                this.DATACV11.title = data.title;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV11)
    }

    // function will send data to info cv11 service
    getDataCv11(data,url){
        this.infocv11service.loadJsondata_cv11(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv11.json';
        console.log(link);
        this.infocv11service.saveJsondata(link,this.DATACV11)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV11").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV11").style.display = "none"; 
    }
 }