import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv3Module } from '../../Models/cv3/cv3.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv3Service {
  private data_cv3 = new BehaviorSubject<any>( new Cv3Module() );
  load_data_cv3 = this.data_cv3.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv3( json_data_cv3, url){
    json_data_cv3['url']= url;
    this.data_cv3.next(json_data_cv3);
  }

  saveJsondata(url:string,json_data_cv3){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv3);
  }
}
