import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv9Service } from '../../../services/cv9/info-cv9.service';
import { Cv9Module, block } from '../../../Models/cv9/cv9.module';

@Component({
    selector: '.app-blog-componentv9',
    templateUrl: './blog_component_v9.component.html',
    styleUrls: ['./blog_component_v9.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV9 implements OnInit  {
    public BLOCK = new block();
    public DATACV9= new Cv9Module();
    public url:any;

    constructor( private infocv9service: InfoCv9Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV9
        this.infocv9service.load_data_cv9.subscribe( 
            data => {
                this.DATACV9.title = data.title;
                this.DATACV9.description = data.description;
                this.DATACV9.blocks = data.blocks;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV9)
    }

    // function will send data to info cv9 service
    getDataCv9(data,url){
        this.infocv9service.loadJsondata_cv9(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv9.json';
        console.log(link);
        this.infocv9service.saveJsondata(link,this.DATACV9)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    addBlock(){
        this.DATACV9.blocks.push(this.BLOCK);
        this.saveData();
    }
    deleteBlock(index){
        this.DATACV9.blocks.splice(index,1);
    }
    openModal(){
        document.getElementById("componentV9").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV9").style.display = "none"; 
    }
 }