import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv4Module } from '../../Models/cv4/cv4.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv4Service {
  private data_cv4 = new BehaviorSubject<any>( new Cv4Module() );
  load_data_cv4 = this.data_cv4.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv4( json_data_cv4, url){
    json_data_cv4['url']= url;
    this.data_cv4.next(json_data_cv4);
  }

  saveJsondata(url:string,json_data_cv4){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv4);
  }
}
