import { Component, OnInit } from '@angular/core';
import * as dataCv15 from './jsonFiles/app-blog-componentv15.json';
import { BlogComponentV15 } from '../.././components/blog_component/blog_component_v15/blog_component_v15.component';
import * as dataCv16 from './jsonFiles/app-blog-componentv16.json';
import { BlogComponentV16 } from '../.././components/blog_component/blog_component_v16/blog_component_v16.component';
import * as dataCv11 from './jsonFiles/app-blog-componentv11.json';
import { BlogComponentV11 } from '../.././components/blog_component/blog_component_v11/blog_component_v11.component';
import * as dataCv14 from './jsonFiles/app-blog-componentv14.json';
import { BlogComponentV14 } from '../.././components/blog_component/blog_component_v14/blog_component_v14.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  constructor(public blogcomponentv15: BlogComponentV15,
              public blogcomponentv16: BlogComponentV16,
              public blogcomponentv11: BlogComponentV11,
              public blogcomponentv14: BlogComponentV14,
              
              private route: ActivatedRoute,) { }

  dataComponenetV15: any = (dataCv15 as any).default;
  dataComponenetV16: any = (dataCv16 as any).default;
  dataComponenetV11: any = (dataCv11 as any).default;
  dataComponenetV14: any = (dataCv14 as any).default;

  ngOnInit(): void {
    var text= this.route.routeConfig.component.name; // component Name 
    var folderJson = text.split('Com')[0].toLowerCase()+"/jsonFiles"; // convert component Name to component Folder + "/jsonFiles"
    //console.log(folderJson);
    this.blogcomponentv15.getDataCv15(this.dataComponenetV15,folderJson);
    this.blogcomponentv16.getDataCv16(this.dataComponenetV16,folderJson);
    this.blogcomponentv11.getDataCv11(this.dataComponenetV11,folderJson);
    this.blogcomponentv14.getDataCv14(this.dataComponenetV14,folderJson);
  }

}
