import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Cv11Module } from '../../Models/cv11/cv11.module'
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class InfoCv11Service {
  private data_cv11 = new BehaviorSubject<any>( new Cv11Module() );
  load_data_cv11 = this.data_cv11.asObservable();
  public host:string = "http://localhost:3000";
  constructor(private httpClient:HttpClient) { }
  
  // will update vriable value by data recieved
  loadJsondata_cv11( json_data_cv11, url){
    json_data_cv11['url']= url;
    this.data_cv11.next(json_data_cv11);
  }

  saveJsondata(url:string,json_data_cv11){
    let link = this.host+"/api/update?url="+url;
    console.log(link);
    return this.httpClient.post(link, json_data_cv11);
  }
}
