import { Component, OnInit } from '@angular/core';
import * as dataCv8 from './jsonFiles/app-blog-componentv8.json';
import { BlogComponentV8 } from '../.././components/blog_component/blog_component_v8/blog_component_v8.component';
import * as dataCv3 from './jsonFiles/app-blog-componentv3.json';
import { BlogComponentV3 } from '../.././components/blog_component/blog_component_v3/blog_component_v3.component';
import * as dataCv5 from './jsonFiles/app-blog-componentv5.json';
import { BlogComponentV5 } from '../.././components/blog_component/blog_component_v5/blog_component_v5.component';
import * as dataCv1 from './jsonFiles/app-blog-componentv1.json';
import { BlogComponentV1 } from '../.././components/blog_component/blog_component_v1/blog_component_v1.component';
import * as dataCv7 from './jsonFiles/app-blog-componentv7.json';
import { BlogComponentV7 } from '../.././components/blog_component/blog_component_v7/blog_component_v7.component';
import * as dataCv2 from './jsonFiles/app-blog-componentv2.json';
import { BlogComponentV2 } from '../.././components/blog_component/blog_component_v2/blog_component_v2.component';
import * as dataCv6 from './jsonFiles/app-blog-componentv6.json';
import { BlogComponentV6 } from '../.././components/blog_component/blog_component_v6/blog_component_v6.component';
import * as dataCv4 from './jsonFiles/app-blog-componentv4.json';
import { BlogComponentV4 } from '../.././components/blog_component/blog_component_v4/blog_component_v4.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(public blogcomponentv8: BlogComponentV8,
              public blogcomponentv3: BlogComponentV3,
              public blogcomponentv5: BlogComponentV5,
              public blogcomponentv1: BlogComponentV1,
              public blogcomponentv7: BlogComponentV7,
              public blogcomponentv2: BlogComponentV2,
              public blogcomponentv6: BlogComponentV6,
              public blogcomponentv4: BlogComponentV4,
              
              private route: ActivatedRoute,) { }

  dataComponenetV8: any = (dataCv8 as any).default;
  dataComponenetV3: any = (dataCv3 as any).default;
  dataComponenetV5: any = (dataCv5 as any).default;
  dataComponenetV1: any = (dataCv1 as any).default;
  dataComponenetV7: any = (dataCv7 as any).default;
  dataComponenetV2: any = (dataCv2 as any).default;
  dataComponenetV6: any = (dataCv6 as any).default;
  dataComponenetV4: any = (dataCv4 as any).default;

  ngOnInit(): void {
    var text= this.route.routeConfig.component.name; // component Name 
    var folderJson = text.split('Com')[0].toLowerCase()+"/jsonFiles"; // convert component Name to component Folder + "/jsonFiles"
    // //console.log(folderJson);
    this.blogcomponentv8.getDataCv8(this.dataComponenetV8,folderJson);
    this.blogcomponentv3.getDataCv3(this.dataComponenetV3,folderJson);
    this.blogcomponentv5.getDataCv5(this.dataComponenetV5,folderJson);
    this.blogcomponentv1.getDataCv1(this.dataComponenetV1,folderJson);
    this.blogcomponentv7.getDataCv7(this.dataComponenetV7,folderJson);
    this.blogcomponentv2.getDataCv2(this.dataComponenetV2,folderJson);
    this.blogcomponentv6.getDataCv6(this.dataComponenetV6,folderJson);
    this.blogcomponentv4.getDataCv4(this.dataComponenetV4,folderJson);
  }

}
