import { Component, OnInit } from '@angular/core';
import * as dataCv15 from './jsonFiles/app-blog-componentv15.json';
import { BlogComponentV15 } from '../.././components/blog_component/blog_component_v15/blog_component_v15.component';
import * as dataCv12 from './jsonFiles/app-blog-componentv12.json';
import { BlogComponentV12 } from '../.././components/blog_component/blog_component_v12/blog_component_v12.component';
import * as dataCv13 from './jsonFiles/app-blog-componentv13.json';
import { BlogComponentV13 } from '../.././components/blog_component/blog_component_v13/blog_component_v13.component';
import * as dataCv11 from './jsonFiles/app-blog-componentv11.json';
import { BlogComponentV11 } from '../.././components/blog_component/blog_component_v11/blog_component_v11.component';
import * as dataCv9 from './jsonFiles/app-blog-componentv9.json';
import { BlogComponentV9 } from '../.././components/blog_component/blog_component_v9/blog_component_v9.component';
import * as dataCv10 from './jsonFiles/app-blog-componentv10.json';
import { BlogComponentV10 } from '../.././components/blog_component/blog_component_v10/blog_component_v10.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {

  constructor(public blogcomponentv15: BlogComponentV15,
              public blogcomponentv12: BlogComponentV12,
              public blogcomponentv13: BlogComponentV13,
              public blogcomponentv11: BlogComponentV11,
              public blogcomponentv9: BlogComponentV9,
              public blogcomponentv10: BlogComponentV10,
              
              private route: ActivatedRoute,) { }

  dataComponenetV15: any = (dataCv15 as any).default;
  dataComponenetV12: any = (dataCv12 as any).default;
  dataComponenetV13: any = (dataCv13 as any).default;
  dataComponenetV11: any = (dataCv11 as any).default;
  dataComponenetV9: any = (dataCv9 as any).default;
  dataComponenetV10: any = (dataCv10 as any).default; 

  ngOnInit(): void {
    var text= this.route.routeConfig.component.name; // component Name 
    var folderJson = text.split('Com')[0].toLowerCase()+"/jsonFiles"; // convert component Name to component Folder + "/jsonFiles"
    //console.log(folderJson);
    this.blogcomponentv15.getDataCv15(this.dataComponenetV15,folderJson);
    this.blogcomponentv12.getDataCv12(this.dataComponenetV12,folderJson);
    this.blogcomponentv13.getDataCv13(this.dataComponenetV13,folderJson);
    this.blogcomponentv11.getDataCv11(this.dataComponenetV11,folderJson);
    this.blogcomponentv9.getDataCv9(this.dataComponenetV9,folderJson);
    this.blogcomponentv10.getDataCv10(this.dataComponenetV10,folderJson);
  }

}
