import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv6Service } from '../../../services/cv6/info-cv6.service';
import { Cv6Module } from '../../../Models/cv6/cv6.module'

@Component({
    selector: '.app-blog-componentv6',
    templateUrl: './blog_component_v6.component.html',
    styleUrls: ['./blog_component_v6.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV6 implements OnInit  {

    public DATACV6= new Cv6Module();
    public url:any;

    constructor( private infocv6service: InfoCv6Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV6
        this.infocv6service.load_data_cv6.subscribe( 
            data => {
                this.DATACV6.description = data.description;
                this.DATACV6.title_button = data.title_button;
                this.DATACV6.link_button = data.link_button;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV6)
    }

    // function will send data to info cv6 service
    getDataCv6(data,url){
        this.infocv6service.loadJsondata_cv6(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv6.json';
        console.log(link);
        this.infocv6service.saveJsondata(link,this.DATACV6)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV6").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV6").style.display = "none"; 
    }
 }