import { Component, OnInit, Injectable  } from '@angular/core';
import { InfoCv8Service } from '../../../services/cv8/info-cv8.service';
import { Cv8Module } from '../../../Models/cv8/cv8.module'

@Component({
    selector: '.app-blog-componentv8',
    templateUrl: './blog_component_v8.component.html',
    styleUrls: ['./blog_component_v8.component.css']
})

@Injectable({
    providedIn: 'root'
})

export class BlogComponentV8 implements OnInit  {

    public DATACV8= new Cv8Module();
    public url:any;

    constructor( private infocv8service: InfoCv8Service){ }
    ngOnInit() {
        //console.log('getOnit');
        // START : this function will put value from BehaviorSubject and put it into DATACV8
        this.infocv8service.load_data_cv8.subscribe( 
            data => {
                this.DATACV8.link_image = data.link_image;
                this.DATACV8.title = data.title;
                this.DATACV8.sub_title = data.sub_title;
                this.DATACV8.text_button = data.text_button;
                this.DATACV8.link_button = data.link_button;
                this.url = data.url;
            });
        // END
        //console.log(this.DATACV8)
    }

    // function will send data to info cv8 service
    getDataCv8(data,url){
        this.infocv8service.loadJsondata_cv8(data,url);
    }

    saveData(){
        let link = '/'+this.url+'/app-blog-componentv8.json';
        console.log(link);
        this.infocv8service.saveJsondata(link,this.DATACV8)
        .subscribe((data:any)=>{
            this.closeModal();
        }
        ,
        err=>{
            console.log("error"+err);
        })
    }
    openModal(){
        document.getElementById("componentV8").style.display = "table"; 
    }

    closeModal(){
        document.getElementById("componentV8").style.display = "none"; 
    }
 }